import react, { useEffect } from 'react'
import {useHistory} from 'react-router-dom'
import {useState} from 'react'
import  './NavBar.css'
const NavBar = () => {
    const [show,setHandleShow] = useState(false)
    const history = useHistory()
    const transitionNavBar = () => {
        if(window.scrollY > 100){
            setHandleShow(true)
        }else{
            setHandleShow(false)
        }
    }
    useEffect(() => {
        window.addEventListener("scroll",transitionNavBar)
        return(() => window.removeEventListener("scroll",transitionNavBar))
    },[])
        return (
        <div className = {`nav ${show && 'nav_black'}`}>

            <div className = "nav_contents">
                <img onClick = {() => history.push("/")}  className = "nav_logo" 
                src = "http://assets.stickpng.com/images/580b57fcd9996e24bc43c529.png" alt = "netflix-logo"></img>
                <img onClick = {() => history.push("/profile")} className = "nav_avatar" 
                src = "data:image/png;base64," alt = "netflix-avatar"></img>
            </div>
        </div>
    )
}

export default NavBar; 